import graphme from '@unitz/graphme';

import hasuraClient from '@vl/mod-clients/hasuraApp';

import GetTransactionAggregate from 'unitz-gql/transaction/GetTransactionAggregate.gql';

graphme.GqlBuilder.loadDocument({
  GetTransactionAggregate
});

class TransactionAggregateCount extends graphme.BaseModel {
  static DEFINITION = graphme.Definition.create({
    name: 'TransactionAggregateCount',
    schema: {
      count: Number
    },
    nodes: [
      ['min', 'TransactionModel'],
      ['max', 'TransactionModel']
    ],
    baseQuery: 'GetTransactionAggregate',
    selection: '{ count }',

    GQL_ACTIONS: {
      GET: 'aggregate'
    },
    getClient: hasuraClient.getClient
  });
}

graphme.model({ TransactionAggregateCount });

class TransactionAggregateModel extends graphme.BaseModel {
  static DEFINITION = graphme.Definition.create({
    name: 'TransactionAggregate',
    schema: {
      // aggregate: {
      //   count: Number,
      //   min: {
      //     id: String,
      //   },
      //   min: {
      //     id: String,
      //   },
      // },
      // nodes: [{
      //   id: String,
      //   advisor_id: String,
      //   user_id: String,
      //   session_id: String,
      // }],
    },
    nodes: [
      ['nodes', 'TransactionModel.Collection', { usePlanSync: true }],
      ['aggregate', 'TransactionAggregateCount', { usePlanSync: true }]
    ],
    key: '',
    baseQuery: 'GetTransactionAggregate',
    selection: `{
      nodes {
        id
      }
    }`,

    GQL_ACTIONS: {
      GET: 'transaction_aggregate'
    },
    getClient: hasuraClient.getClient
  });
}

graphme.model({ TransactionAggregateModel });

export default TransactionAggregateModel;
